










import { Component, Vue } from 'vue-property-decorator'

@Component({
})
export default class Home extends Vue {
  private tab: string = 'labby'
  changeTab () {
    if (this.tab === 'labby') {
      this.$router.push('/home/lobby')
    } else {
      this.$router.push('/home/usercenter')
    }
  }
  created () {
    if (this.$route.name === 'UserCenter') {
      this.tab = 'usercenter'
    }
  }
}
